body {
  font-family: "GothamPro", Open Sans, sans-serif;
  font-size: 36px;
  font-weight: 500;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 2400px;
  background-color: #2b2d42;
}

.inner {
  padding: 0 160px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.wrapper {
  height: calc(100vh - 400px);
}

.block {
  display: flex;
}

.header {
  position: relative;
  height: 200px;
}

.header__logo {
  float: left;
  margin-top: 63px;
}

.header__soc {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.header__nav {
  float: right;
}

.logo {
  font-family: "GothamPro", Open Sans, sans-serif;
  font-size: 90px;
  font-weight: 900;
  line-height: 1;
  color: #fff;
  text-transform: uppercase;
  display: inline-block;
}

.soc {
  font-size: 0;
}

.soc__item {
  vertical-align: middle;
  display: inline-block;
}

.soc__item:not(:first-child) {
  margin-left: 50px;
}

.soc__link {
  width: 100px;
  height: 100px;
  text-align: center;
  line-height: 90px;
  border: 5px solid rgba(255, 255, 255, 0.25);
  border-radius: 50%;
  display: block;
}

.soc__link .icon {
  font-size: 31px;
  vertical-align: middle;
  display: inline-block;
}

.nav__list {
  font-size: 0;
}

.nav__item {
  vertical-align: middle;
  display: inline-block;
}

.nav__item:not(:first-child) {
  margin-left: 130px;
}

.nav__link {
  padding-top: 70px;
  color: #fff;
  font-family: "GothamPro", Open Sans, sans-serif;
  font-size: 48px;
  font-weight: 500;
  line-height: 1;
  border-top: 10px solid transparent;
  display: block;
}

.nav__item.is-active .nav__link {
  border-color: #e32434;
}

[data-tabs] {
  overflow: hidden;
}

[data-tabs] [data-tabs-control] {
  cursor: pointer;
}

[data-tabs] [data-tabs-control][disabled], [data-tabs] [data-tabs-control].is-disabled {
  cursor: not-allowed;
  user-select: none;
}

[data-tabs] [data-tabs-wrapper] {
  position: relative;
}

[data-tabs] [data-tabs-container] {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}

[data-tabs] [data-tabs-container].is-open {
  position: relative;
  opacity: 1;
  visibility: visible;
}

.footer {
  height: 200px;
}

.steps {
  font-size: 0;
  counter-reset: section;
}

.steps__item {
  position: relative;
  opacity: 0.5;
  box-shadow: inset 3px 0 0 rgba(255, 255, 255, 0.15);
  vertical-align: top;
  display: inline-block;
}

.steps__item.is-open {
  opacity: 1;
}

.steps__item.is-active {
  opacity: 1;
  background-color: #fff;
}

.steps__link {
  position: relative;
  padding: 120px 47px 20px;
  color: #fff;
  height: 200px;
  min-width: 415px;
  font-size: 48px;
  font-weight: 500;
  line-height: 1;
  display: block;
}

.steps__item.is-active .steps__link {
  color: #2b2d42;
}

.steps__link:before {
  position: absolute;
  top: 20px;
  left: 47px;
  color: #e32434;
  counter-increment: section;
  content: counter(section);
  font-size: 72px;
  font-weight: 700;
  line-height: 1;
}

.steps__item:nth-of-type(-n+9) .steps__link:before {
  content: "0" counter(section) ".";
}

.steps__check {
  position: absolute;
  top: 0;
  right: 0;
  height: 100px;
  width: 100px;
  text-align: center;
  line-height: 100px;
  background-color: #e32434;
}

.steps__check .icon {
  font-size: 38px;
  vertical-align: middle;
  display: inline-block;
}

.steps__lock {
  position: absolute;
  top: 16px;
  right: 28px;
}

[data-modal] {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  padding: 20px;
  opacity: 0;
  visibility: hidden;
  text-align: center;
  font-size: 0;
  white-space: nowrap;
  transform: translate3d(0, 0, 0);
  transition: opacity 0.3s, visibility 0.3s;
}

[data-modal]:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

[data-modal].is-open {
  opacity: 1;
  visibility: visible;
}

[data-modal-container] {
  position: relative;
  z-index: 1;
  min-width: 725px;
  max-width: 780px;
  max-height: 1140px;
  white-space: normal;
  text-align: left;
  font-size: 24px;
  color: #fff;
  vertical-align: middle;
  overflow: hidden;
  background-color: #fff;
  box-shadow: 0 0 25px rgba(43, 45, 66, 0.25);
  display: inline-block;
}

[data-modal-close] {
  position: absolute;
  top: 17px;
  right: 10px;
  padding: 20px;
  background-color: transparent;
  font-size: 0;
}
