//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

// FuturaFuturis
@font-face {
	@include fontdefault(normal, 900, 'GothamPro');
	@include fontpath('GothamPro-Black', 'GothamPro-Black', GothamPro-Black); }
@font-face {
	@include fontdefault(italic, 900, 'GothamPro');
	@include fontpath('GothamPro-BlackItalic', 'GothamPro-BlackItalic', GothamPro-BlackItalic); }

@font-face {
	@include fontdefault(normal, 700, 'GothamPro');
	@include fontpath('GothamPro-Bold', 'GothamPro-Bold', GothamPro-Bold); }
@font-face {
	@include fontdefault(italic, 700, 'GothamPro');
	@include fontpath('GothamPro-BoldItalic', 'GothamPro-BoldItalic', GothamPro-BoldItalic); }

@font-face {
	@include fontdefault(normal, 500, 'GothamPro');
	@include fontpath('GothamPro-Medium', 'GothamPro-Medium', GothamPro-Medium); }
@font-face {
	@include fontdefault(italic, 500, 'GothamPro');
	@include fontpath('GothamPro-MediumItalic', 'GothamPro-MediumItalic', GothamPro-MediumItalic); }

@font-face {
	@include fontdefault(normal, 400, 'GothamPro');
	@include fontpath('GothamPro', 'GothamPro', GothamPro); }
@font-face {
	@include fontdefault(italic, 500, 'GothamPro');
	@include fontpath('GothamPro-Italic', 'GothamPro-Italic', GothamPro-Italic); }

@font-face {
	@include fontdefault(normal, 300, 'GothamPro');
	@include fontpath('GothamPro-Light', 'GothamPro-Light', GothamPro-Light); }
@font-face {
	@include fontdefault(italic, 500, 'GothamPro');
	@include fontpath('GothamPro-LightItalic', 'GothamPro-LightItalic', GothamPro-LightItalic); }


// GothamProNarrow
@font-face {
	@include fontdefault(normal, 500, 'GothamProNarrow');
	@include fontpath('GothamProNarrow-Medium', 'GothamProNarrow-Medium', GothamProNarrow-Medium); }
@font-face {
	@include fontdefault(normal, 700, 'GothamProNarrow');
	@include fontpath('GothamProNarrow-Bold', 'GothamProNarrow-Bold', GothamProNarrow-Bold); }
